body {
  background-color: #364549;
}

.wrapper {
  display: grid;
  grid-auto-rows: minmax(1fr, 300px);
  grid-template-columns: repeat(8, 1fr);
  margin-top: 5px;
  -ms-flex-pack: stretch;
      justify-content: stretch;
}

.grid-item {
  font-size: 18px;
  grid-column: 3 / span 5;
  background-color: #576f75;
  color: #e3e3e3;
  padding: 20px;
}

.grid-img {
  grid-column: 3 /span 5;
}

.code-snippet {
  width: 100%;
  height: auto;
}



